<script>
export default {
    props: {
        href: {
            type: String,
            default: ''
        },
        allowInsecure: {
            type: Boolean,
            default: false,
        },
        to: {
            type: Object,
            default: null
        },
        name: {
            type: String,
            default: ''
        },
        params: {
            type: Object,
            default: () => ({})
        },
        def_class: {
            type: String
        }
    },
    computed: {
        routerLinkTo({ name, params }) {
            return {
                name,
                params,
                ...(this.to || {})
            }
        }
    },
    created() {
        this.validateProps()
    },
    methods: {
        validateProps() {
            if (process.env.NODE_ENV === 'production') return

            if (this.href) {
                if (!/^\w+:/.test(this.href)) {
                    return console.warn(
                        "Invalid <BaseLink> href: ${this.href}.\nIf you're trying to link to a local URL, provide at least a name."
                    )
                }
                if (!this.allowInsecure && !/^https/.test(this.href)) {
                    return console.warn(
                        "Insecure <BaseLink> href: ${this.href}.\nWhen linking to external sites, always prefer https URLs. If this site does not offer SSL, explicitly add the allow-insecure attribute on <BaseLink>."
                    )
                }
            } else {
                if (!this.name && !this.to) {
                    return console.warn(
                        "Invalid <BaseLink> props:\n\n${JSON.stringify(this.$props, null, 2)}\n\nEither a 'name' or 'to' is required for internal links, or an 'href' for external links."
                    )
                }
            }
        }
    }
}
</script>

<template>
    <a
        v-if="href"
    v-bind="$attrs"
    :href="href"
    target="_blank"
    :class="def_class"
    >
        <slot />
    </a>
    <router-link
        v-else
    v-bind="$attrs"
    :to="routerLinkTo"
    :class="def_class"
    >
        <slot />
    </router-link>
</template>
